nav {
  background-color: #f0f0f0; /* Light gray background */
  padding: 1rem;
  display: flex;
  justify-content: center; /* Centers the content horizontally */
}

nav ul {
  list-style: none;
  display: flex;
  gap: 1.5rem; /* Space between menu items */
  margin: 0;
  padding: 0;
}

nav ul li a {
  text-decoration: none;
  color: blue;
  font-weight: bold;
}

nav ul li a:hover {
  color: darkblue; /* Change color on hover for better interactivity */
}
