.App {
  text-align: center;
}

.App-logo {
  height: 10vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #1b7bae;
  min-height: 20vh;
  display: flex;
 /* flex-direction: column;*/
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
.Main {
  background-color:#4033a5;
  min-height: 50vh;
  display: flex;
  /*flex-direction: column;*/
 /* align-items: center;
  justify-content: center;*/
  font-size: calc(10px + 2vmin);
  color:rgb(255, 255, 255)
}

.Footer {
  background-color:#4033a5;
  min-height: 15vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
